@import url(https://fonts.googleapis.com/css2?family=Shadows+Into+Light&display=swap);
body {
  background-color: rgb(68, 226, 226);
  font-family: "Shadows Into Light", cursive;
}
.navbar {
    background-color: black;
    width: 100%;
    height: 75px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.navbar--container {
    height: 100%;
    width: 40%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.btn {
    text-decoration: none;
    color:white;
    height:100%;
    width:150px;
    font-size: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
}

.btn:hover {
    background-color:#BAE3F6;
    color:black;
}




 .headerContainer{
    background-color: #BAE3F6;
    text-align: center;
    padding-top: 20px;
}

h1 {
    text-transform: uppercase;
}
.cards--container {
    height: 100%;
    width: 100%;
    background-color: #BAE3F6;
    margin-top: -22px;
    padding-top:100px;
    padding-left: 50px;
    display:grid;
    grid-template-columns: 30% 30% 30%;
    grid-template-rows: 500px 500px 500px 500px;
    grid-column-gap: 50px;
    grid-row-gap: 20px;
}

.card {
    border-radius: 20px;
    background-color: rgb(0, 0, 0);
    padding: 70px 30px 30px 30px;
}

h3 {
    color:white;
    margin-bottom: 30px;
    text-transform: uppercase;
    font-size: 35px;
}

.image {
    width: 99%;
    height: 260px;
    background-color: black;
    position: relative;
}

img {
    height: 100%;
    width: 100%;
    background-size: cover;
    background-position: center;
    border-radius: 15px;
}

p {
    opacity: 0;
    position: absolute;
    bottom: 0%;
    left: 5%;
    color: #fff;
}

.image:hover p {
    opacity: 1;
    bottom: 20%;
    transition: all 0.5s ease-in-out;
    background-color:rgba(0, 0, 0, 0.7);
}

.image:hover img {
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    transition: 0.5s;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
